import React from "react";
import styles from "./header.module.css";
import {Container} from "react-bootstrap";
import {Link, useLocation, useNavigate, useParams} from "react-router-dom";
import Languages from "./Languages";
import {useTranslation} from "react-i18next";
import ResponseNav from "../ResponseNav";
import logo from '../../svg/Armeniantree.png'
const Header = () => {
    const {t} = useTranslation();
    const {pathname} = useLocation();
    const navigate = useNavigate();
    return (
        <>
            <section className={styles.media}>
                <ResponseNav/>
            </section>
            <section className={styles.web} style={{maxWidth: 1750}}>
                
                    <div className={styles.header}>
                        <div className={styles.logo} >
                            <img src={logo} onClick={()=>navigate('/')} alt="" />
                        </div>
                        <div className={styles.menu_items}>
                            <ul className={styles.ul}>
                                <li>
                                    <Link
                                        to="/"
                                        className={`${pathname == "/" ? styles.linkMian : null}`}
                                    >
                                        {t("home").toUpperCase()}
                                    </Link>
                                </li>
                                <li style={{width: 100}}>
                                    <Link
                                        to="/about"
                                        className={`${
                                            pathname == "/about" ? styles.linkMian : null
                                        }`}
                                        
                                    >
                                        {t("about").toUpperCase()}
                                    </Link>
                                </li>
                                <li style={{width: 155}}>
                                    <Link
                                        to="/our"
                                        className={`${pathname == "/our" ? styles.linkMian : null}`}
                                    >
                                        {t("work").toUpperCase()}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to="/project"
                                        className={`${
                                            pathname == "/project" ? styles.linkMian : null
                                        }`}
                                    >
                                        {t("project").toUpperCase()}
                                    </Link>
                                </li>

                                <li>
                                    <Link
                                        to="/news"
                                        className={`${
                                            pathname == "/news" ? styles.linkMian : null
                                        }`}
                                    >
                                        {t("news").toUpperCase()}
                                    </Link>
                                </li>
                                <li>
                                    <Link
                                        to="/contact"
                                        className={`${
                                            pathname == "/contact" ? styles.linkMian : null
                                        }`}
                                    >
                                        {t("contact").toUpperCase()}
                                    </Link>
                                </li>
                                <li>
                                <Languages/>
                                </li>
                            </ul>
                        </div>
                        {/*
          <div className={styles.language}>Arm Eng</div> */}
                    {/* <div className={styles.languages} >
                        <Languages/>
                        </div> */}
                    </div>
          
            </section>
        </>
    );
};

export default Header;
