import React, {useEffect, useState} from "react";
import styles from "./footer.module.css";
import {Container, Row, Col} from "react-bootstrap";
import {Link, useLocation} from "react-router-dom";
import {useTranslation} from "react-i18next";
import {ReactComponent as Tw} from "../../svg/tw.svg";
import {ReactComponent as LI} from "../../svg/li.svg";
import Fb from "../../svg/fb.png";
import Phone from "../../svg/Phone.png";
import Location from "../../svg/location.png";
import Mail from "../../svg/Mail.png";
import axios from "axios";
import {keys} from "../../keys";
import ampik from "../../svg/Ampikner.png"
import logo from '../../svg/Armeniantree.png'
import footampik from "../../svg/Ampiknerfooter.png"
const Footer = () => {
    const {pathname} = useLocation();
    const {t} = useTranslation();
    const [data, setData] = useState(null);
    useEffect(() => {
        axios
            .get(`${keys.API_URI}/api/v1/contacts`)
            .then((resp) => {
                setData(resp.data);
            })
            .catch((error) => {
                console.log(error);
            });

        console.log("object");
    }, []);

    return (
        <section className={styles.bacground}>
            
            <Container>
                    
                    
                  
                <Row className="justify-content-md-center">
                    
                    
                <Col sm={12} md={12} lg={12} >
                        <div className={styles.footlogo}>
                            {/* <p className={styles.disc}>{t("disc")}</p> */}
                            <img src={logo} alt=''/>
                        </div>
                    </Col>
                </Row>
                <Row>
                
                    
                    <Col sm={12} md={6} lg={3}>
                        <ul className={styles.ul}>
                            <li>
                                <h5 className={styles.titleFoo}>{t("info")}</h5>
                            </li>
                            <li>
                                <Link
                                    to="/"
                                    className={`${pathname == "/" ? styles.linkMian : null}`}
                                >
                                    {t("home")}
                                </Link>
                            </li>

                            <li>
                                <Link
                                    to="/project"
                                    className={`${
                                        pathname == "/project" ? styles.linkMian : null
                                    }`}
                                >
                                    {t("project")}
                                </Link>
                            </li>
                            <li>
                                <Link
                                    to="/our"
                                    className={`${pathname == "/our" ? styles.linkMian : null}`}
                                >
                                    {t("work")}
                                </Link>
                            </li>
                        </ul>
                    </Col>

                    <Col sm={12} md={6} lg={3}>
                        <ul className={styles.ul} style={{marginTop: 44}}>
                            {/* <li>
                                <h5 className={styles.titleFoo}>{t("contact")}</h5>
                            </li> */}
                            <li>
                                <Link
                                    to="/news"
                                    className={`${pathname == "/news" ? styles.linkMian : null}`}
                                >
                                    {t("news")}
                                </Link>
                            </li>
                            <li>
                                <Link
                                    to="/about"
                                    className={`${pathname == "/about" ? styles.linkMian : null}`}
                                >
                                    {t("about")}
                                </Link>
                            </li>
                            <li>
                                <Link
                                    to="/contact"
                                    className={`${
                                        pathname == "/contact" ? styles.linkMian : null
                                    }`}
                                >
                                    {t("contact")}
                                </Link>
                            </li>

                        </ul>
                    </Col>
                    <Col sm={12} md={6} lg={4}>
                        <ul className={styles.network_block}>
                            {/* <li>
                                <a href={data !== null && data[0]?.facebook} target="blank">
                                    <Fb/>
                                </a>
                            </li>
                            <li>
                                <a href={data !== null && data[0]?.linkedin} target="blank">
                                    <LI/>
                                </a>
                            </li> */}
                            <li>
                            <h5 className={styles.titleFoo}>{t("contact")}</h5>
                            </li>
                        </ul>
                        <ul className={styles.ull}>
                            <li>
                                <img src={Location} style={{width: 34}}  alt=""/>
                                <p>{data !== null && data[0]?.location}</p>
                            </li>
                            <li>
                            <img src={Phone} style={{width: 23, marginLeft: 6, marginRight: 6}}  alt=""/>

                                <p>{'+1 23 456 789'}</p>
                            </li>
                            <li>
                                <img src={Mail} style={{width: 25, marginLeft: 5, marginRight: 5}}  alt=""/>
                                <p>{data !== null && data[0]?.email}</p>
                            </li>
                        </ul>
                    </Col>
                    <Col sm={12} md={6} lg={2}>
                    <ul className={styles.network_block}>
                            <li>
                                <a href={data !== null && data[0]?.facebook} target="blank">
                                    {/* <Fb/> */}
                                    <img src={Fb} style={{width: 30}}/>
                                </a>
                            </li>
                            <li>
                                <a href={data !== null && data[0]?.linkedin} target="blank">
                                    <LI/>
                                </a>
                            </li>
                
                        </ul>
                    </Col>
                </Row>
                <Row >
                <Col sm={12} md={12} lg={12} >
                        <div className={styles.footlogoa}>
                            <p className={styles.disc}>{t("disc")}</p>
                        </div>
                    </Col>
                </Row>
            </Container>
            <img src={footampik} alt="" style={{position: 'absolute', bottom: 0, width: '100%', zIndex: -1}}/>
        </section>
    );
};

export default Footer;
