import React, { useState, useEffect } from "react";
import i18next from "i18next";
import cookies from "js-cookie";
import css from "./header.module.css";
import Armenia from "../../svg/armenian.png"
import english from "../../svg/english.png"
const Languages = () => {
  const languages = [
    { id: 1, lang: "en" },
    { id: 2, lang: "am" },
  ];

  const currentLang = cookies.get("i18next");
  const [activeLang, setActiveLang] = useState(currentLang);

  const selectlanguages = (lang, id) => {
    i18next.changeLanguage(lang);
    setActiveLang(lang);
    localStorage.setItem("lang", lang);
  };
  useEffect(() => {
    if (localStorage.getItem("lang") !== 'am' || localStorage.getItem("lang") !== 'en') {
      i18next.changeLanguage('en');
      setActiveLang('en');
      localStorage.setItem("lang", "en");
    }
  }, [])

  return (
    <div style={{ display: 'inline' }}>
      {activeLang === 'am' && <img src={Armenia} alt='' onClick={() => selectlanguages('en')} style={{ width: 44 }} />}
      {activeLang === 'en' && <img src={english} alt='' onClick={() => selectlanguages('am')} style={{ width: 44 }} />}
      {/* <select
        onChange={(e) => selectlanguages(e.target.value)}
        className={css.selectMain}
      >
        {languages.map(({ id, lang }) => {
          return (
            <option
              key={id}
              value={lang}
              onClick={() => selectlanguages(lang, id)}
              className={lang === activeLang ? "language active" : "language"}

            >
              {lang.toUpperCase()}
            </option>
          );
        })}
      </select> */}
    </div>
  );
};

export default Languages;
